.home_container {
  height: 100vh;
  background-color: #5383e8;
  display: flex;
  flex-direction: column;
}

.home_container .home_title {
  display: flex;
  justify-content: center;
  margin-top: 200px;
  margin-bottom: 100px;
}

.home_container .home_title .home_title_text {
  font-weight: bold;
  font-size: 40px;
}

.home_container .home_search {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home_container .home_search .home_search_container {
  display: flex;
  background-color: #ffffff;
  width: 50%;
  border-radius: 10px;
  padding: 10px 20px;
}

.home_container .home_search .home_search_container .home_search_input {
  flex: 1;
  min-width: 10px;
  font-size: 16px;
  border: 0px;
}

.home_container .home_search .home_search_container .home_search_submit {
  background-color: #5383e8;
  border: 0px;
  font-weight: bold;
  color: #ffffff;
  padding: 5px 5px;
}