.summoner_container {
  background-color: #efefef;
  border-radius: 10px;
  margin: 30px;
  padding: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.summoner_container img {
  max-width: 20%;
  width: auto;
  height: auto;
  margin-right: 5%;
}

.summoner_container .summoner_info {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.summoner_container .summoner_info .summoner_nickname {
  font-weight: bold;
  font-size: 24px;
}

.summoner_container .summoner_info .summoner_type {
  font-weight: bold;
  color: #888888;
}

.summoner_container .summoner_info .summoner_tierinfo {
  
}

.summoner_container .summoner_info .summoner_tierinfo span {
  margin-right: 10px;
  color: #888888;
}

.summoner_container .summoner_info .summoner_winloseinfo {
  
}

.summoner_container .summoner_info .summoner_winloseinfo span {
  margin-right: 10px;
  color: #888888;
}